import React from "react";
import moment from "moment";
import { Progress } from "antd";
import { FormItem } from "../../request/types";

const getGender = (type?: string | number) => {
  switch (type) {
    case "1":
    case 1:
      return "";
    case "2":
    case 2:
      return "";
    default:
      return "";
  }
};

// 
export const tableColumns: any = [
  {
    title: "",
    dataIndex: "id",
    width: 50,
  },
  {
    title: "",
    dataIndex: "patient_name",
    render: (text?: string) => (
      <div style={{ color: "#1abc9c" }}>{text?.toString() || "/"}</div>
    ),
  },
  {
    title: "ID",
    dataIndex: "uuid",
    render: (text?: string | number) => text?.toString() || "/",
  },
  {
    title: "",
    dataIndex: "patient_num",
  },
  {
    title: "",
    dataIndex: "pinyin",
  },
  {
    title: "",
    dataIndex: "gender",
    render: (text?: string | number) => getGender(text),
  },
  {
    title: "",
    dataIndex: "birthday",
    render: (text: any) => {
      if (text) {
        return (
          <div>
            {Number(moment(new Date()).format("YYYY")) -
              Number(moment(text).format("YYYY"))}
          </div>
        );
      } else return "/";
    },
  },
  {
    title: "",
    dataIndex: "created_at",
    render: (text: any) => {
      if (text) {
        return <div>{moment(text).format("YYYY-MM-DD")}</div>;
      } else {
        return "/";
      }
    },
  },
  {
    title: "",
    dataIndex: "schedule",
    width: 100,
    render: (text: string | number) => {
      if (typeof text === "string") {
        return "";
      } else {
        return (
          <div>
            <Progress
              percent={text}
              strokeWidth={6}
              style={{ fontSize: 12 }}
              strokeColor="#1abc9c"
            />
          </div>
        );
      }
    },
  },
  {
    title: "",
    dataIndex: "this_patient_visit",
    render: (text: any) => text?.name,
  },
  {
    title: "",
    dataIndex: "expired_visit",
    render: (text: any) => text?.toString() || "/",
  },
  // {
  //   title: "",
  //   dataIndex: "challenge_count",
  //   render: (text: any) => text?.toString() || "/",
  // },
];
// 
export const genderConfig: ConfigType<Partial<FormItem>> = {
  placeholder: "",
  elements: [
    { name: "", value: "0" },
    { name: "", value: "1" },
    { name: "", value: "2" },
  ],
};
// 
export const diagnosticConfig: ConfigType<FormItem> = {
  placeholder: "",
  elements: [],
};

export type ConfigType<T> = {
  placeholder: string;
  elements?: Array<T>;
};
